





















import {Component, Vue} from 'vue-property-decorator';
import {BufferStream} from "buffer-stream-js";
import Axios, {AxiosResponse} from "axios"
import CurrentUserSessionType from "@/struct/CurrentUserSessionType";


@Component({
    components: {},
})
export default class Home extends Vue {
    timeInterval!: number
    maxVolPerTime!: number
    StatusConnect!: string

    created() {
        this.timeInterval = 1000
        this.maxVolPerTime = 0
        this.ChangeStatus(false)
        this.ConnectToServer()
    }

    private ChangeStatus(status: boolean): void {
        if (status == true) {
            this.StatusConnect ='<font color="green">Online<font>'
        } else {
            this.StatusConnect = '<font color="green">Offline<font>'
        }
        this.$forceUpdate()
    }

    private OutputPlayerAudio(audioBuffer: Uint8Array): void {
        const outputBlob = new Blob([audioBuffer], {type: "audio/webm;codecs=opus"})

        const fileReader = new FileReader();
        fileReader.readAsDataURL(outputBlob);
        fileReader.onloadend = () => {
            const base64String = fileReader.result;
            //socket.emit("voice", base64String);
            const audio = new Audio(base64String as string);
            audio.play();
        };
    }

    private AnalyticVoice(stream: any) {
        const audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
        const analyser = audioContext.createAnalyser();
        const microphone = audioContext.createMediaStreamSource(stream);
        const javascriptNode: ScriptProcessorNode = audioContext.createScriptProcessor(2048, 1, 1);
        const documentVolLevelElement: HTMLDivElement = document.querySelector('.volLevel') as HTMLDivElement
        const documentMaxVolLevelElement: HTMLDivElement = document.querySelector('.maxLevel') as HTMLDivElement


        // Init PIDS HTML
        const pidsElements: HTMLDivElement[] = []
        let pids = document.querySelectorAll('.pid');
        pids.forEach(pid => {
            pidsElements.push(pid as HTMLDivElement)
        })
        //

        // ANIMATION PIDS HTML
        const colorPids = (vol: number) => {
            let amout_of_pids = Math.round(vol / 5);
            let elem_range = pidsElements.slice(0, amout_of_pids)
            for (let i = 0; i < pidsElements.length; i++) {
                pidsElements[i].style.backgroundColor = "#e6e7e8";
            }
            for (let i = 0; i < elem_range.length; i++) {
                elem_range[i].style.backgroundColor = "#69ce2b";
            }
        }

        analyser.smoothingTimeConstant = 0.8;
        analyser.fftSize = 1024;

        microphone.connect(analyser);
        analyser.connect(javascriptNode);
        javascriptNode.connect(audioContext.destination);
        javascriptNode.addEventListener('audioprocess', () => {
            const array = new Uint8Array(analyser.frequencyBinCount);
            analyser.getByteFrequencyData(array);
            let values = 0;

            const length = array.length;
            for (let i = 0; i < length; i++) {
                values += (array[i]);
            }

            const average = values / length;

            // Write Max Vol
            if (average > 10 && average > this.maxVolPerTime) {
                this.maxVolPerTime = average
            }

            documentMaxVolLevelElement.innerHTML = String(Math.round(this.maxVolPerTime))
            documentVolLevelElement.innerHTML = String(Math.round(average))
            colorPids(average);
        })
    }

    private Start() {
        navigator.mediaDevices.getUserMedia({audio: true}).then((stream) => {
            this.AnalyticVoice(stream)
            const madiaRecorder = new (window as any).MediaRecorder(stream);
            let audioChunks: any[] = [];
            this.maxVolPerTime = 0

            madiaRecorder.start();

            madiaRecorder.addEventListener("dataavailable", (event: { data: Blob }) => {
                audioChunks.push(event.data);
                // const fileReader = new FileReader();
                // fileReader.readAsDataURL(event.data);
                // fileReader.onloadend = () => {
                //     if (!this.userStatus.microphone || !this.userStatus.online) return;
                //
                //     const base64String = fileReader.result;
                //     console.log(base64String)
                //     //socket.emit("voice", base64String);
                //     const audio = new Audio(base64String as string);
                //     audio.play();
                // };
            });

            madiaRecorder.addEventListener("stop", () => {
                if (audioChunks.length > 0 && this.maxVolPerTime > 0) {
                    const mimeType = audioChunks[0].type
                    const audioBlob = new Blob(audioChunks, {type: mimeType});


                    audioBlob.arrayBuffer().then(arrayBuffer => {
                        const audioBuffer = new Uint8Array(arrayBuffer);

                        (window as any).SendPlayerTick(audioBuffer);
                    })
                }
                audioChunks = [];
                this.maxVolPerTime = 0
                madiaRecorder.start();
                setTimeout(() => {
                    madiaRecorder.stop();
                }, this.timeInterval);
            });

            setTimeout(() => {
                madiaRecorder.stop();
            }, this.timeInterval);
        });

    }

    private ConnectToServer(): void {
        const CONST_X_VIEXPO_INTERNAL_TOKEN = 'x-viexpo-internal-token-test'
        const CONST_X_VIEXPO_SESSION_TOKEN_BODY = 'x-viexpo-session-token'

        const ToGetway_FromUnknown_AuthSocket = 0
        const FromGetway_AuthSocket_Response = 1
        const ToGetway_FromInternal_GetUser = 2
        const ToGetway_FromInternal_GetUserFromSession = 3
        const FromGetway_ToInternal_GetUserResponse = 4
        const ToGetway_FromPlayer_GetMovementServer = 5
        const FromGetway_FromPlayer_GetMovementServerResponse = 6

        const ToMovement_ToPlayerScene_PlayerTick = 9991
        const FromMovement_FromPlayerScene_PlayerJoin = 9992
        const FromMovement_FromPlayerScene_PlayerLeave = 9993
        const FromMovement_FromPlayerScene_PlayerList = 9994
        const FromMovement_FromPlayerScene_PlayerPosition = 9995
        const FromSound_FromPlayerScene_PlayerVoice = 9996


        const ReadPlayerFromPlayerList = (buffer: BufferStream, startOffset: number, hasPosition = false) => {
            const userUUIDLen = (hasPosition == true ? 0 : 36)

            let uuid = ''
            if (hasPosition == false) {
                buffer.offset = startOffset
                uuid = buffer.readUtf8String(userUUIDLen)
            }
            buffer.offset = startOffset + userUUIDLen + 0
            const id = buffer.readUInt32LE()

            const position = {
                x: 0,
                y: 0,
                z: 0
            }

            buffer.offset = startOffset + userUUIDLen + 4 + 0
            position.x = buffer.readFloatLE()

            buffer.offset = startOffset + userUUIDLen + 4 + 4
            position.y = buffer.readFloatLE()

            buffer.offset = startOffset + userUUIDLen + 4 + 8
            position.z = buffer.readFloatLE()

            buffer.offset = startOffset + userUUIDLen + 4 + 12
            const state = buffer.readUInt8()

            return {
                uuid: uuid,
                id: id,
                position: position,
                state: state
            }
        }

        const SendPlayerTick = (position: { x: number, y: number, z: number }, state: number, voiceBody: Uint8Array) => {
            const array = new Uint8Array(4 + 12 + 1 + voiceBody.byteLength)

            const methodBuffer = new Uint8Array(new Uint32Array([ToMovement_ToPlayerScene_PlayerTick]).buffer)

            array.set(methodBuffer, 0) // before 0, after 4

            array.set(new Uint8Array(new Float32Array([position.x]).buffer), 4) // before 4, after 8
            array.set(new Uint8Array(new Float32Array([position.y]).buffer), 4 + 4) // before 8, after 12
            array.set(new Uint8Array(new Float32Array([position.z]).buffer), 4 + 8) // before 12, after 16

            array.set(new Uint8Array([state]), 4 + 12) // before 16, after 17

            array.set(voiceBody, 4 + 12 + 1) // before 17, after 4

            WebSocketClient.send(array);
        };

        (window as any).SendPlayerTick = (chunk: Uint8Array) => SendPlayerTick({x: 0, y: 0, z: 0}, 0, chunk);

        const WebSocketClient = new WebSocket('wss://getway.dev.viexpo.ru:10010');
        WebSocketClient.binaryType = "arraybuffer"
        WebSocketClient.onmessage = (buffer: any) => {
            const message: BufferStream = new BufferStream(buffer.data)

            message.offset = 0
            const method = message.readInt32LE()
            switch (method) {
                case FromMovement_FromPlayerScene_PlayerList:
                    message.offset = 4
                    const countPlayers = message.readUInt32LE()

                    const users = []
                    for (let index = 0; index < countPlayers; index++) {
                        const user = ReadPlayerFromPlayerList(message, 4 /* Method */ + 4 /* Count */ + (index * (36 /* UUID */ + 4 /* ID */ + (4 + 4 + 4) /* Position */ + 1 /* Flags */)))
                        users.push(user)
                    }
                    console.log('Method: FromMovement_FromPlayerScene_PlayerList, count: ' + countPlayers, users)
                    break
                case FromMovement_FromPlayerScene_PlayerJoin:
                    const user = ReadPlayerFromPlayerList(message, 4)
                    console.log("User Join: " + user.uuid)
                    break
                case FromMovement_FromPlayerScene_PlayerLeave:
                    message.offset = 4
                    const uuid = message.readUtf8String(36)
                    console.log("User Leave: " + uuid)
                    break
                case FromSound_FromPlayerScene_PlayerVoice:
                    const user_from_voice = ReadPlayerFromPlayerList(message, 4, true)
                    // Parse Voice Body
                    const voiceBody = new Uint8Array(message.buffer.buffer, 21)
                    // Play Voice Body
                    this.OutputPlayerAudio(voiceBody)
                    console.log('FromSound_FromPlayerScene_PlayerVoice => ', user_from_voice)
                    break
                default:
                    console.log('Method: Unknown')
                    break
            }
        }
        WebSocketClient.onclose = () => {

            this.ChangeStatus(false)
            setTimeout(() => {
                this.ConnectToServer()
            }, 1000)
        }
        WebSocketClient.onopen = () => {

            this.ChangeStatus(true)
            Axios.get('https://getway.dev.viexpo.ru/api/account/fake/get').then((res: AxiosResponse<{ response: { user: CurrentUserSessionType, session_uuid: string } }>) => {
                const sessionUUID = res.data.response.session_uuid
                const array = new Uint8Array(4 + 36 + 1 + 36)

                const methodBuffer = new Uint8Array(new Uint32Array([ToGetway_FromUnknown_AuthSocket]).buffer)
                const sessionBuffer = new TextEncoder().encode(sessionUUID)
                const locationUUIDBuffer = new TextEncoder().encode("123456789012345678901234567890123456") // 36 LEN

                array.set(methodBuffer, 0)
                array.set(sessionBuffer, 4)
                array.set(new Uint8Array([0]), 4 + 36)
                array.set(locationUUIDBuffer, 4 + 36 + 1)

                WebSocketClient.send(array);
            })


        };
    }

}
